.cardRow {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.card {
    background-color: rgb(182, 182, 182);
    width: 90%;
    margin: 2%;
    height: auto;
    padding: 10px;
    border-radius: 15px;
    color: rgb(65, 65, 65);
    word-wrap: break-word;
}